<template>
  <div class="modal fade" id="insertFreightModal" data-backdrop="static" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">
            选择菜品：
            <div class="fs-12">
              提示: 发票单中已出现的菜品不会在此展示
            </div>
          </h5>

        </div>
        <div class="modal-body pt-0 pb-0">
          
          <div class="">

            <table class="table table-top-line-none" v-if="havent.length > 0">
              <tr>
                <td width="79px" class="text-center">序号</td>
                <td width="98px">货号</td>
                <td>描述</td>
              </tr>
              <tr v-for="(f, index) in havent" :key="f.id" 
                :class="{ 'item-choise': (f.id == choised.id) }"
                @click="itemChoised(f.id)"
              >
                <td class="text-center">
                  {{ index + 1 }}
                </td>
                <td>
                  {{ f.num }}
                </td>
                <td>
                  {{ f.named }}
                </td>
              </tr>
            </table>
            <div v-else class="text-center p-2 mt-4">
              全部菜品都已加到发票中了。
            </div>

          </div>

        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" @click="closeFreight">取消</button>
          <button type="button" class="btn btn-main" @click="insertSubmit">加入到发票中</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import conn from '@/service/conn'
export default {
  name: 'InsertFreightModal',
  props: {
    haved: {
      type: Array,
      default: [ 0 ]
    }
  },
  data() {
    return {
      freights: [],
      havent: [ ],
      choised: {}
    }
  },
  mounted() {
    conn.freight.getList_Freight({}, true).then(res => {
      if (conn.judge_response(res)) {
        this.choised = res[0]
        this.freights = res
        this.loadHaventFreight()
      }
    })
  },
  methods: {
    loadHaventFreight() {
      
      const ids = this.haved.map((e) => e.id)

      this.havent = this.freights.filter((e) => {
        if (tool.inList(e.id, ids)) {
          return false
        }
        return true
      })
      
    },
    insertSubmit() {
      this.$emit('reciverInsert', this.choised)
      this.closeFreight()
    },
    closeFreight() {
      tool.closeModal('insertFreightModal')
    },
    openInsertFreight() {
      tool.showModal('insertFreightModal')
    },
    itemChoised(id) {
      const res = this.havent.filter((e) => {
        if (e.id == id) { return true } return false
      })
      this.choised = res[0]
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


